.search {
  height: 200px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
  background-color: transparent;
  margin-bottom: 10px;
}

.searchEnter {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

.searchInput {
  height: 40px;
  width: 200px;
  font-size: 18px;
  border: none;
  border-bottom: 1px #DDA0DD solid;
  outline: none;
  background-color: #FFF0F5;
}

.searchBtn {
  width: 100px;
  height: 40px;
  -ms-flex-item-align: center;
      align-self: center;
  font-size: 16px;
  border: none;
  background-color: #800080;
  color: #FFF0F5;
  outline: none;
}

.searchBtn:hover {
  background-color: #DDA0DD;
  color: #FFF0F5;
}



